const maps = window.google.maps;
const geocoder = new maps.Geocoder();

export default async function (data) {
  const branches = {};
  const requests = [];
  data.branches.forEach((branch) => {
    const id = branch.id;
    const address = branch.values[6];
    const applications = [];
    requests.push(new Promise((resolve, reject) => {
      geocoder.geocode({'address': address}, (results, status) => {
        // console.log(results, status);
        if (status === 'OK') {
          const lat = results[0].geometry.location.lat();
          const lng = results[0].geometry.location.lng();
          const type = results[0].geometry.location_type;
          if (type === 'ROOFTOP' || type === 'RANGE_INTERPOLATED') {
            branches[id] = {address, lat, lng, applications};
          } else {
            console.log('Incorrect address ' + address + ' skipped');
          }
          // resolve(status);
        } else {
          console.log(address, status);
          // alert('Error: ' + status + ' while geocoding');
          // reject(status);
        }
        resolve();
      });
    }));
  });

  await Promise.all(requests);

  // console.log(data.applications);
  data.applications.forEach((application) => {
    const branchId = application.values[3][0].recordId;
    const status = +application.values[5][0];
    const criticality = +application.values[10][0];
    if (branches[branchId]) branches[branchId].applications.push({status, criticality});
  });

  const points = [];
  for (let key in branches) {
    branches[key].id = key;
    points.push(branches[key]);
  }

  return points;
}
