import React from 'react'
import GoogleMapReact from 'google-map-react';
import Supercluster from 'points-cluster';
import Marker from './Marker';
import Container from 'react-bootstrap/Container';
import './style.css';

export default class Map extends React.Component {
  static defaultProps = {
    center: {
      lat: 61.25,
      lng: 73.38
    },
    zoom: 4
  };

  state = {
    clusters: [],
  };

  mapOptions = {
    center: Map.defaultProps.center,
    zoom: Map.defaultProps.zoom,
  };

  onMapChange = (mapOptions) => {
    this.mapOptions = mapOptions;
    this.createClusters(this.mapOptions);
  };

  createClusters(mapOptions) {
    const clusters = mapOptions.bounds
      ? this.getClusters(mapOptions).map(({ wx, wy, numPoints, points }) => ({
          lat: wy,
          lng: wx,
          numPoints,
          id: `${numPoints}_${points[0].id}`,
          points,
        }))
      : [];
    this.setState({ clusters });
  }

  getClusters(mapOptions) {
    const clusters = Supercluster(this.props.data);
    return clusters(mapOptions);
  }

  componentDidUpdate(prevProps) {
    // ..........
    if (prevProps.data.length !== this.props.data.length) this.onMapChange(this.mapOptions);
  }

  render() {
    const clusters = this.state.clusters.map(cluster => {
      return (
        <Marker
          key={cluster.id}
          lat={cluster.lat}
          lng={cluster.lng}
          points={cluster.points}
        />
      );
    });

    return (
      <Container fluid className="map h-100 w-100 px-0">
        <GoogleMapReact
          bootstrapURLKeys={{
            language: 'ru',
            region: 'ru',
          }}
          defaultCenter={Map.defaultProps.center}
          defaultZoom={Map.defaultProps.zoom}
          onChange={this.onMapChange}
          options={(maps) => {
            return {
              disableDefaultUI: true,
            };
          }}
        >
          {clusters}
        </GoogleMapReact>
      </Container>
    );
  }
}
