import React from 'react';
import Container from 'react-bootstrap/Container';
import './style.css';

function getTheMostFrequentValue(arr) {
  const counter = {};
  arr.forEach((item) => {
    counter[item] ? counter[item]++ : counter[item] = 1;
  });

  let maxKey, max = 0;
  for (let key in counter) {
    if (counter[key] > max) {
      max = counter[key];
      maxKey = key;
    }
  }

  return maxKey;
}

export default (props) => {
  const N = props.points.length;
  let side = 20 + (N-1)*2;
  if (side > 100) side = 100;

  const applicationsCriticalities = [];
  props.points.forEach(point => {
    point.applications.forEach(application => {
      applicationsCriticalities.push(application.criticality);
    });
  });

  const criticality = applicationsCriticalities.length ? +getTheMostFrequentValue(applicationsCriticalities) : null;

  let backgroundColor;
  switch (criticality) {
    case 1:
      backgroundColor = 'rgba(186, 57, 57, 0.8)';
      break;
    case 2:
      backgroundColor = 'rgba(23, 217, 150, 0.8)';
      break;
    case 3:
      backgroundColor = 'rgba(81, 175, 43, 0.8)';
      break;
    case 4:
      backgroundColor = 'rgba(84, 213, 255, 0.8)';
      break;
    default:
      backgroundColor = 'rgba(81, 175, 43, 0.8)';
      break;
  }

  return (
    <Container
      className="cluster px-0 justify-content-center align-items-center"
      style={{
        height: side,
        width: side,
        marginTop: -side/2,
        marginLeft: -side/2,
        backgroundColor,
      }}
    >
      <span className="title">{N}</span>
    </Container>
  );
}
